<template>
  <div id="banner">
    <Card v-for="(item,index) in bannerList" :key="index" style="width: 300px;" class="list">        
        <div style="text-align:center">
            <img :src="item.href" width="100%">
            <Button type="warning" style="margin-top: 10px;" @click="deletePic(item.id)">删除</Button>
        </div>     
    </Card>
    <Card>
        <Upload
            multiple
            type="drag"
            :on-success="handleSuccess"
            :format="['jpg','jpeg','png']"
            :max-size="2048"
            action="http://www.pzhuweb.cn/api/file/dealPic">
            <div style="padding: 20px 0">
                <Icon type="ios-cloud-upload" size="52" style="color: #3399ff"></Icon>
                <p>上传轮播图</p>
            </div>
        </Upload>       
        <Button type="primary" @click="uploadPic" style="margin-top: 10px;">确定上传</Button>
    </Card>
  </div>
</template>

<script>
export default {
    name: 'banner',
    data () {
        return {
            imgList: [],
            bannerList: []
        }
    },
    created(){
        this.getBannerList();
    },
    methods: {
        handleSuccess (res, file, fileList) {
            this.imgList.push(res.data)
        },
        uploadPic () {
            if(this.imgList.length == 0){
                this.$Message.error("请先上传图片")
            }else{
                this.$http.post('/file/bannerInsert',{
                    "imgList": this.imgList
                }).then(res => {
                    this.getBannerList();
                    this.$Message.success("上传成功！")
                }).catch(err => {
                    this.$Message.error("出错啦。。。")
                })
            }
        },
        getBannerList () {
            this.$http.get('/file/getBannerList')
                .then(res => {
                    this.bannerList = res.data.msg;
                }).catch(err => {
                    this.$Message.error("出错啦。。。")
                })
        },
        deletePic (id,href) {  
            this.$Modal.confirm({                
                content: '<p>确定删除吗</p>',
                onOk: () => {
                    this.$http.post('/file/deletePic',{
                        'id': id                      
                    }).then(res => {       
                        this.getBannerList();
                        this.$Message.success("删除成功！");
                    }).catch(err => {
                        this.$Message.error("出错啦。。。");
                    })
                },
                onCancel: () => {
                    this.$Message.info('Clicked cancel');
                }
            });
        }
    }
}
</script>
<style lang="scss" scoped>
.bannerWrap{
    width: 50%;
    .demo-carousel{
        height: 200px;        
        color: white;
        background: chocolate;
    }
}
.list{
    display: inline-block;
    margin: 20px;
}
</style>




